import React, { Component } from 'react'
import AppBar from '@material-ui/core/AppBar'
import Toolbar from '@material-ui/core/Toolbar'
import Button from '@material-ui/core/Button'
import Typography from '@material-ui/core/Typography'
import Menu from '@material-ui/core/Menu'
import MenuItem from '@material-ui/core/MenuItem'
import MenuIcon from '@material-ui/icons/Menu'
import { withStyles } from '@material-ui/core/styles'
import { Link } from 'gatsby'

const styles = {
  root: {
    flexGrow: 1,
    marginBottom: 15,
  },
  grow: {
    flexGrow: 1,
  },
  menuButton: {
    marginLeft: -12,
    marginRight: 20,
  },
}

class Header extends Component {
  constructor(props) {
    super(props)
    this.state = {
      isMenuOpen: false,
      anchorEl: null,
    }
  }
  toggleMenu = event => {
    this.setState({
      isMenuOpen: !this.state.isMenuOpen,
      anchorEl: event.currentTarget,
    })
  }

  closeMenu = () => {
    if (this.state.isMenuOpen) {
      this.setState({
        isMenuOpen: false,
      })
    }
  }

  render() {
    const { classes } = this.props
    const { anchorEl, isMenuOpen } = this.state
    return (
      <div className={classes.root}>
        <AppBar position="static">
          <Toolbar>
            <Typography variant="h6" color="inherit" className={classes.grow}>
              <Link className="link" to="/">
                Morphbreed's Page
              </Link>
            </Typography>
            <Menu
              id="render-props-menu"
              open={isMenuOpen}
              anchorEl={anchorEl}
              onClose={this.closeMenu}
            >
              <MenuItem>
                <Link className="link" to="/" onClick={this.toggleMenu}>
                  Home
                </Link>
              </MenuItem>
              <MenuItem>
                <Link className="link" to="/infos" onClick={this.toggleMenu}>
                  Infos
                </Link>
              </MenuItem>
            </Menu>
            <Button
              className="toggle-menu-button"
              aria-label="show menu"
              color="inherit"
              onClick={this.toggleMenu}
            >
              <MenuIcon />
            </Button>
            <nav>
              <Link className="link" to="/">
                <Button color="inherit" aria-label="show entries">
                  Home
                </Button>
              </Link>

              <Link className="link" to="/infos">
                <Button color="inherit" aria-label="show info">
                  Infos
                </Button>
              </Link>
            </nav>
          </Toolbar>
        </AppBar>
      </div>
    )
  }
}

export default withStyles(styles)(Header)
