import React from 'react'
import Card from '@material-ui/core/Card'
import CardHeader from '@material-ui/core/CardHeader'
import CardContent from '@material-ui/core/CardContent'
import CardActions from '@material-ui/core/CardActions'
import CardMedia from '@material-ui/core/CardMedia'
import Button from '@material-ui/core/Button'
import CppImage from '../images/cpp.png'
import CsImage from '../images/cs.png'
import JsImage from '../images/js.png'
import UeImage from '../images/ue.png'
import BlImage from '../images/bl.png'

const entryStyle = {
  marginBottom: 10,
}

const Entry = props => {
  let EntryImage = ''
  let imageTitle = ''

  switch (props.category) {
    case 'cpp':
      EntryImage = CppImage
      imageTitle = 'C++'
      break
    case 'cs':
      EntryImage = CsImage
      imageTitle = 'Computer Science'
      break
    case 'ue':
      EntryImage = UeImage
      imageTitle = 'Unreal Engine'
      break
    case 'js':
      EntryImage = JsImage
      imageTitle = 'JavaScript'
      break
    case 'bl':
    EntryImage = BlImage
    imageTitle = 'Blender'
    break
    default:
  }

  let entryImage = props.category ? (
    <CardMedia
      className="entry-media"
      style={{ minHeight: 150, width: '100%' }}
      image={EntryImage}
      title={imageTitle}
    />
  ) : (
    ''
  )

  if(props.category === "bl") {
    entryImage = <a href="http://www.blender.org/" rel="noopener noreferrer" target="_blank">{entryImage}</a>
  }

  const course = props.courseUrl ? (
    <CardActions style={{ padding: '12px 5px' }}>
      <a
        rel="noopener noreferrer"
        style={{ textDecoration: 'none' }}
        target="_blank"
        href={props.courseUrl}
      >
        <Button size="small" color="primary" aria-label="show course">
          To the Course
        </Button>
      </a>
    </CardActions>
  ) : (
    ''
  )

  let entry = (
    <Card style={entryStyle} className="entry">
      <CardHeader
        style={{ paddingBottom: 5 }}
        title={props.title}
        subheader={props.date}
      />
      {entryImage}
      <CardContent style={{ padding: '5px 16px' }}>{props.content}</CardContent>
      {course}
    </Card>
  )

  return entry;
}

export default Entry
